.news-page {
  &__wrap {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    width: calc(100% - 324px);
    .index__news-list {
      margin-top: 0;
      margin-top: 24px;
    }
    .index__news-item {
      width: 32%;
    }
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 10px 0;
    color: #afafb3;
    font-size: 24px;
    background: #1f1f23;
  }
  &__list {
    display: flex;
    align-items: center;
  }
  &__item {
    margin-right: 20px;
  }
  &__link {
    font-size: 14px;
    &--active {
      color: #00c0ff;
    }
    &:hover {
      color: #fff;
    }
  }
  &__search {
    display: flex;
    width: 100%;
    border-radius: 4px;
    margin-top: 13px;
    input,
    button {
      height: 40px;
      border: none;
    }
    input {
      width: calc(100% - 40px);
      padding: 5px 12px;
      background: rgba(58, 58, 61, 0.5);
      border-radius: 4px 0px 0px 4px;
      font-size: 13px;
      color: #fff;
      &:hover {
        background: #3A3A3D;
      }
    }
    button {
      width: 40px;
      background: #28282D;
      border-radius: 0px 4px 4px 0px;
      &:hover {
        background: #1F1F23;
        svg path {
          fill: #fff;
        }
      }
    }
  }
}

@media (max-width: 940px) {
  .news-page {
    &__wrap {
      flex-direction: column;
    }
    &__content {
      width: 100%;
    }
    &__right-bar {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
}

@media (max-width: 665px) {
  .news-page {
    .index__news-item {
      width: 48%;
    }
  }
}

@media (max-width: 625px) {
  .news-page {
    .news-page__item:nth-child(8) {
      display: none;
    }
  }
}

@media (max-width: 570px) {
  .news-page {
    .news-page__item:nth-child(7) {
      display: none;
    }
  }
}

@media (max-width: 500px) {
  .news-page {
    .news-page__item:nth-child(6) {
      display: none;
    }
  }
}

@media (max-width: 422px) {
  .news-page {
    .news-page__item:nth-child(5) {
      display: none;
    }
  }
}

@media (max-width: 330px) {
  .news-page {
    .news-page__item:nth-child(4) {
      display: none;
    }
  }
}