.index {
  &__main {
    margin: 0 24px;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
  }
  &__news {
    &-top {
      background: #1F1F23;
      border-radius: 4px;
      display: flex;
      align-items: center;
      padding: 9px;
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        margin-right: 8px;
        &:hover {
          svg path {
            fill: #C9C9CB;
          }
        }
      }
    }
    &-setting {
      margin-left: auto;
      svg path {
        fill: #797983;
      }
    }
    &-bottom {
      margin-top: 30px;
    }
    &-title {
      font-weight: 400;
      font-size: 13px;
      color: #AFAFB3;
      margin-bottom: 10px;
      a {
        font-weight: 500;
        font-size: 20px;
        color: #C9C9CB;
        margin-right: 20px;
        &:hover {
          color: #fff;
        }
      }
    }
    &-news {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: #AFAFB3;
      margin-bottom: 8px;
      display: block;
    }
    &-time {
      margin-right: 8px;
      color: #797983;
    }
    &-comm {
      font-size: 12px;
      color: #797983;
      img {
        vertical-align: middle;
        margin-left: 10px;
        margin-right: 4px;
      }
      span {
        color: #AFAFB3;
        margin-left: 4px;
      }
    }
    &-more {
      font-size: 12px;
      color: #797983;
      display: flex;
      align-items: center;
      margin-top: 12px;
      span {
        border-bottom: 0.5px dashed #797983;
      }
      &:hover {
        color: #AFAFB3;
        span {
          border-color: #fff;
        }
        svg path {
          fill: #AFAFB3;
        }
      }
      svg {
        transform: rotate(-90deg);
        margin-left: 8px;
      }
      svg path {
        fill: #797983;
      }
    }
    &-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: 35px;
    }
    &-item {
      width: 48%;
      margin-bottom: 24px;
    }
    &-link {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
    &-block {
      position: relative;
      display: flex;
      &-pinned {
        position: absolute;
        top: 4px;
        right: 4px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
        height: 20px;
        background: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
        border-radius: 2px;
        font-weight: bold;
        font-size: 12px;
        text-transform: uppercase;
        color: #fff;
        svg {
          margin-left: 6px;
        }
        span {
          margin-left: -2px;
          transform: scale(0.833);
        }
      }
      &-btn {
        position: absolute;
        bottom: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        background: rgba(14, 14, 16, 0.7);
        border-radius: 4px;
        svg {
          width: 20px;
          height: 20px;
        }
        &:hover svg path {
          fill: #AFAFB3;
        }
      }
    }
    &-img {
      border-radius: 6px;
      width: 100%;
    }
    &-name {
      font-weight: 500;
      font-size: 15px;
      line-height: 135%;
      letter-spacing: 0.25px;
      color: #C9C9CB;
      margin-top: 8px;
      margin-bottom: 4px;
    }
    &-stat {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 13px;
      letter-spacing: 0.25px;
      color: #797983;
      margin-top: auto;
      img {
        margin-left: 6px;
        margin-right: 4px;
      }
      span {
        font-size: 12px;
        display: flex;
        align-items: center;
      }
    }
    &-text-w {
      margin-left: 3px;
      color: #AFAFB3;
    }
    &-load {
      background: #1F1F23;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 44px;
      font-size: 12px;
      letter-spacing: 0.3px;
      color: #AFAFB3;
      &:hover {
        color: #fff;
        svg path {
          fill: #fff;
        }
      }
      svg {
        transform: rotate(-90deg);
        margin-left: 8px;
      }
    }
  }
}

@media (max-width: 1100px) {
  .index {
    &__wrap {
      flex-wrap: wrap;
    }
    &__left-bar {
      order: 2;
      align-self: flex-start;
    }
    &__right-bar {
      order: 3;
    }
    &__main {
      margin: 0;
      margin-bottom: 24px;
    }
    &__news {
      &-item {
        width: 32%;
      }
    }
  }
}

@media (max-width: 850px) {
  .index {
    &__news {
      &-item {
        width: 48%;
      }
    }
  }
}
@media (max-width: 665px) {
  .index {
    &__wrap {
      justify-content: center;
    }
    &__right-bar,
    &__left-bar {
      width: 100%;
    }
  }
}

@media (max-width: 500px) {
  .index {
    &__news {
      &-item {
        width: 100%;
      }
    }
  }
}