.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-right: 16px;
  background: #1F1F23;
  box-shadow: 0px 2px 2px #0E0E10;
  height: 64px;
  margin-bottom: 24px;
  &__list {
    display: flex;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    margin-left: 12px;
    &--none {
      display: none;
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    min-width: 40px;
    border-radius: 4px;
    font-size: 13px;
    letter-spacing: 0.25px;
    &:hover {
      background: #28282D;
    }
    &:hover svg path {
      fill: #fff;
    }
    &--mail {
      position: relative;
      &::before {
        content: '13';
        position: absolute;
        top: 0;
        right: 0;
        padding: 0 3px;
        font-weight: 500;
        font-size: 10px;
        line-height: 13px;
        color: #fff;
        background: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
        border-radius: 4px;
      }
    }
    &--user {
      border-radius: 50%;
      &:hover ~ .header__sublist--active {
        display: block;
      }
    }
    &--exit {
      padding: 0 6px;
      svg {
        margin-right: 8px;
      }
    }
  }
  &__sublist {
    display: none;
  }
  &__sublist--active {
    position: absolute;
    top: 100%;
    right: 0;
    min-width: 246px;
    background: #1F1F23;
    box-shadow: 0px 0px 10px rgba(14, 14, 16, 0.5);
    border-radius: 4px;
    padding: 8px 10px;
    padding-top: 12px;
    z-index: 1;
  }
  &__subitem-head {
    display: flex;
    align-items: center;
    padding-bottom: 8px;
    border-bottom: 1px solid #28282D;
    margin-bottom: 8px;
    a {
      display: flex;
      align-items: center;
    }
    div {
      margin-left: 8px;
      display: flex;
      flex-direction: column;
    }
  }
  &__subitem-text--name {
    font-size: 13px;
    font-weight: bold;
    letter-spacing: 0.25px;
  }
  &__subitem-text {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.3px;
  }
  &__subitem {
    display: flex;
  }
  &__sublink {
    display: flex;
    align-items: center;
    width: 100%;
    font-size: 13px;
    padding: 8px 5px;
    border-radius: 4px;
    svg {
      margin-right: 8px;
    }
    &:hover {
      background: #28282D;
      color: #fff;
    }
    &:hover svg path {
      fill: #fff;
    }
  }
}

@media (max-width: 700px) {
  .header {
    &__logo {
      display: flex;
      align-items: center;
      img {
        width: 120px;
      }
    }
  }
}