.pagination {
  display: flex;
  align-items: center;
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
  }
  &__link {
    font-size: 14px;
    padding: 3px 10px;
    background: #1f1f23;
    border-radius: 4px;
    &:hover {
      color: #fff;
    }
  }
}
