.forum-page {
  &__wrap {
    display: flex;
    justify-content: space-between;
  }
  &__main {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-right: 20px;
  }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 24px;
    &-title {
      color: #c9c9cb;
      font-size: 24px;
      font-weight: 400;
      margin: 0;
    }
    &-btn {
      border: 1px solid #3c3d41;
      background: #1f1f23;
      border-radius: 4px;
      color: #c9c9cb;
      font-size: 12px;
      padding: 8px 10px;
      &:hover {
        background: #343434;
        color: #fff;
      }
    }
  }
  &__section {
    margin-bottom: 20px;
    &-title {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #2b2c32;
      border-radius: 4px 4px 0 0;
      padding: 8px 0;
      font-size: 18px;
      &:hover {
        color: #fff;
      }
    }
    &-list {
      background: #1f1f23;
      border-radius: 0 0 4px 4px;
    }
    &-item {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      padding: 15px;
      border-top: 1px solid #121215;
      a:hover {
        color: #fff;
      }
      img {
        width: 36px;
        border-radius: 50%;
      }
    }
    &-links {
      margin-left: 10px;
      margin-right: auto;
      &-title {
        display: block;
        font-size: 16px;
        line-height: 18px;
      }
      &-list {
        padding-left: 10px;
      }
      &-item {
        display: flex;
        align-items: center;
        position: relative;
        padding-left: 10px;
        margin-top: 8px;
        &::before {
          content: '';
          position: absolute;
          top: calc(50% - 2px);
          left: 0;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background: #c9c9cb;
        }
      }
      &-link {
        font-size: 12px;
      }
    }
    &-block-text {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      color: #61616f;
      font-size: 12px;
      margin: 0 10px;
      &--mobile {
        display: none;
        color: #61616f;
        font-size: 12px;
      }
    }
    &-block-user {
      display: flex;
      font-size: 12px;
      min-width: 230px;
      p {
        color: #61616f;
      }
      div {
        margin-left: 10px;
      }
    }
  }
  &__right-bar {
    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #3c3d41;
      background: #1f1f23;
      border-radius: 4px;
      color: #c9c9cb;
      font-size: 12px;
      padding: 8px 10px;
      &:hover {
        background: #343434;
        color: #fff;
      }
    }
    &-block {
      min-width: 300px;
      background: #1f1f23;
      border-radius: 4px;
      margin-top: 24px;
    }
    &-title {
      display: flex;
      align-items: center;
      justify-content: center;
      border-bottom: 1px solid #121215;
      border-radius: 4px 4px 0 0;
      font-size: 18px;
      color: #afafb3;
      background: #2b2c32;
      padding: 8px 0;
      margin: 0;
    }
    &-item {
      display: flex;
      padding: 10px 10px;
      a:hover {
        color: #fff;
      }
    }
    &-img {
      display: flex;
      align-items: flex-start;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      margin-right: 10px;
      img {
        width: 100%;
      }
    }
    &-theme {
      display: flex;
      font-size: 14px;
    }
    &-info {
      display: flex;
      align-items: center;
      font-size: 12px;
      color: #61616f;
      a {
        margin-right: 4px;
      }
      img {
        margin: 0 4px;
      }
    }
    &-stat-block {
      padding: 10px 10px;
    }
    &-stat {
      display: flex;
      justify-content: space-between;
      font-size: 13px;
      color: #afafb3;
    }
  }
  &__users-online {
    background: #1f1f23;
    border-radius: 0 0 4px 4px;
    padding: 10px 20px;
    color: #afafb3;
    margin-top: 24px;
    &-title {
      font-size: 18px;
      font-weight: 400;
      margin: 0;
    }
    &-info {
      font-size: 14px;
    }
    &-list {
      margin-top: 15px;
      display: flex;
      flex-wrap: wrap;
    }
    &-item {
      display: flex
    }
    &-link {
      font-size: 13px;
    }
  }
}

@media (max-width: 1200px) {
  .forum-page {
    &__section-block-text {
      display: none;
    }
    &__section-block-text--mobile {
      display: block;
    }
  }
}

@media (max-width: 1000px) {
  .forum-page {
    &__wrap {
      flex-direction: column;
    }
  }
}

@media (max-width: 650px) {
  .forum-page {
    &__section-block-user {
      display: none;
    }
  }
}