.breadcrumbs {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  &__item {
    position: relative;
    margin-right: 20px;
    &:first-child::before {
      display: none;
    }
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: -10px;
      width: 3px;
      height: 6px;
      background: url(../img/arrow-right.svg) no-repeat center;
    }
  }
  &__link {
    font-size: 13px;
    color: #797983;
    &:hover {
      color: #fff;
    }
  }
}