.matches {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    @include c-grey0 ();
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.15px;
    &:hover {
      color: #fff;
    }
  }
  &__text {
    display: flex;
    align-items: center;
    color: #797983;
    font-size: 13px;
    letter-spacing: 0.25px;
    margin-bottom: 0px;
    margin-right: -90px;
    svg {
      margin-right: 12px;
    }
  }
  &__toggle {
    display: flex;
    align-items: center;
    &-left,
    &-right {
      width: 40px;
      height: 40px;
      background: #1F1F23;
      border-radius: 4px;
      &:hover {
        background: #0E0E10;
        svg path {
          fill: #797983;
        }
      }
    }
    &-right svg {
      transform: rotate(180deg);
    }
    &-dots {
      margin: 0 5px;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      background: #1F1F23;
      &--active {
        background: #39393E;
      }
    }
  }
  &__more {
    display: none;
  }
  &__slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
  }
  &__slide {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 19%;
    height: 116px;
    padding: 0 20px;
    background-image: linear-gradient(180deg, rgba(#1F1F23, 0.6) 0%, rgba(#1F1F23, 0.6) 100%), url(../img/matches/bcg.jpg);
    background-size: cover;
    &--live::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0; 
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(255, 36, 36, 0) 65.62%, rgba(219, 36, 36, 0.4) 100%), linear-gradient(180deg, rgba(14, 14, 16, 0) 0%, #1F1F23 100%);
    }
    &-left,
    &-middle,
    &-right {
      @include c-grey1 ();
      display: flex;
      flex-direction: column;
      align-items: center;
      font-weight: 500;
      font-size: 13px;
      z-index: 1;
    }
    &-middle {
      width: 52px;
    }
    &-left,
    &-right {
      width: calc(50% - 26px);
      img {
        margin-bottom: 2px;
      }
    }
    &-middle {
      align-self: flex-start;
    }
    &-dota,
    &-cup {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &-dota {
      width: 24px;
      height: 24px;
      background: rgba(14, 14, 16, 0.7);
      border-radius: 0px 0px 4px 4px;
      &:hover {
        background: rgba(14, 14, 16, 0.3);
        svg path {
          opacity: .5
        }
      }
    }
    &-cup {
      width: 30px;
      height: 30px;
      background: #28282D;
      border-radius: 4px;
      margin-top: 19px;
      margin-bottom: 11px;
      &:hover {
        background: #1F1F23;
        svg path {
          fill: #AFAFB2;
        }
      }
    }
    &-coeff {
      font-size: 12px;
      margin: 0;
      margin-top: 2px;
      padding: 2px 4px;
      border-radius: 4px;
      &--green {
        background: rgba(55, 173, 109, 0.05);
      }
      &--red {
        background: rgba(219, 36, 36, 0.05);
      }
    }
    &-live {
      color: #fff;
      font-size: 12px;
      font-weight: bold;
      margin: 20px 0;
      margin-top: 22px;
      background: linear-gradient(227.8deg, #FB6F43 3.56%, #B41431 92.44%);
      border-radius: 2px;
      padding: 0 5px;
      transform: scale(0.8);
    }
    &-time {
      text-align: center;
      font-weight: 500;
      font-size: 13px;
      color: #AFAFB3;
      margin-top: 11px;
      margin-bottom: 14px;
      span {
        display: block;
        font-weight: 400;
        font-size: 11px;
        letter-spacing: 0.3px;
        color: #797983;
        color: red;
        margin-top: -2px;
      }
    }
  }
}

@media (max-width: 1100px) {
  .matches {
    &__slide {
      width: 24%;
      &:nth-child(5) {
        display: none;
      }
    }
  }
}

@media (max-width: 880px) {
  .matches {
    &__slide {
      width: 32%;
      &:nth-child(1) {
        display: none;
      }
    }
  }
}

@media (max-width: 670px) {
  .matches {
    &__toggle,
    &__text {
      display: none;
    }
    &__title {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 14px 0;
      width: 100%;
      background: #1F1F23;
      border-radius: 4px 4px 0px 0px;
    }
    &__slider {
      margin-top: 0;
      flex-direction: column;
    }
    &__slide {
      width: 100%;
      border-top: 1px solid #28282D;
    }
    &__more {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px 0;
      background: #1F1F23;
      border-radius: 0px 0px 4px 4px;
      font-size: 13px;
      &:hover {
        color: #fff;
        svg path {
          fill: #fff;
        }
      }
      svg {
        margin-left: 8px;
        transform: rotate(-90deg);
      }
    }
  }
}