body {
	min-width: 320px;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
	color: #afafb2;
	font-size: 15px;
	line-height: 1.5;
	letter-spacing: 0.5px;
	box-sizing: border-box;
	background: #0E0E10; }

iframe {
	width: 100%; }

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden; }

*, *:before, *:after {
	box-sizing: border-box;
	outline: none; }

ul {
	list-style: none;
	margin: 0;
	padding: 0; }

@mixin c-grey0 () {
  color: #C9C9CB; }
@mixin c-grey1 () {
  color: #AFAFB3; }
@mixin c-grey2 () {
  color: #797983; }
@mixin c-grey3 () {
  color: #61616F; }

a {
	@include c-grey1 ();
	text-decoration: none; }
p {
	margin: 0; }

button {
	padding: 0;
	border: none;
	cursor: pointer; }

.text-color--green {
	color: #37AD6D; }

.text-color--red {
	color: #D42C2C; }
