.forum-section {
  &__table {
  }
  &__list {
    margin: 10px 0;
    background: #1f1f23;
    border-radius: 4px;
  }
  &__item {
    display: flex;
    align-items: center;
    padding: 5px 10px;
    border-top: 1px solid #121215;
    &--locked {
      background: #222328;
    }
    &--first {
      background: #000;
      font-size: 14px;
      color: #2e73a0;
      border: none;
      .forum-section__col-4 {
        justify-content: flex-end;
      }
    }
    a:hover {
      color: #fff;
    }
  }
  &__col-1 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    margin-right: 15px;
    a {
      display: flex;
      width: 36px;
    }
    img {
      width: 100%;
      border-radius: 4px;
    }
  }
  &__col-2 {
    width: 100%;
  }
  &__title {
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name {
    font-size: 12px;
    color: #61616f;
  }
  &__mes--mobile {
    display: none;
  }
  &__col-3 {
    min-width: 160px;
    margin: 0 15px;
    p {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 13px;
      color: #afafb3;
    }
  }
  &__col-4 {
    min-width: 200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
      display: flex;
      width: 36px;
    }
    img {
      width: 100%;
      border-radius: 4px;
    }
    div {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      a {
        width: auto;
        font-size: 14px;
      }
      p {
        font-size: 12px;
        color: #61616f;
      }
    }
  }
}

@media (max-width: 800px) {
  .forum-section {
    &__col-3,
    &__col-4 {
      display: none;
    }
    &__mes--mobile {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      span {
        color: #61616f;
      }
    }
  }
}

@media (max-width: 450px) {
  .forum-section {
    &__mes--mobile {
      flex-direction: column;
    }
  }
}